import React from 'react';
import styled from 'styled-components';
import SmoothScroll from 'smooth-scroll';
import SectionHome from '../components/HomePageComponents/SectionHome';
import SectionProjects from '../components/HomePageComponents/SectionProjects';
import SectionAboutMe from '../components/HomePageComponents/SectionAboutMe';
import SectionContact from '../components/HomePageComponents/SectionContact';
//yarn add smooth-scroll

const HomePage = () => {
  const scroll = new SmoothScroll ('a[href*="#"]', {
    speed: 1000,
  });
  console.log (scroll);

  return (
    <HomeWrapper>
      <div className="container">
        <SectionHome />
        <SectionProjects />
        <SectionAboutMe />
        <SectionContact />
      </div>
    </HomeWrapper>
  );
};

const HomeWrapper = styled.nav`
`;

export default HomePage;
